* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.box {
  justify-content: center;
  display: flex;
}

input {
  background-color: #f0f0f0;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
}

button[selectedDate] {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

.timer {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  font-size: 24px;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  margin: 0 20px;
  display: flex;
}

.value {
  color: #333;
  text-align: center;
  font-size: 48px;
  font-weight: bold;
}

.label {
  color: #999;
  text-align: center;
  margin-top: 10px;
  font-size: 18px;
}

